body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    outline: none;
    box-sizing: border-box;
}

html, body{
    height: 100vh;
    background-color: #34343b !important;
    font-family: 'Eras Demi ITC', system-ui,
        -apple-system,
        "Segoe UI",
        Roboto,
        "Helvetica Neue",
        "Noto Sans",
        "Liberation Sans",
        Arial,
        sans-serif,
        "Apple Color Emoji",
        "Segoe UI Emoji",
        "Segoe UI Symbol",
        "Noto Color Emoji" !important;
    padding: 0px !important;
    overflow-x: hidden;
    scroll-behavior: smooth;
}

ul::-webkit-scrollbar {
    width: 15px;
    /* Set the width of the scrollbar */
}

:root{
    --bs-dark-rgb: 46,46,53 !important;
}

#root{
    overflow-x: hidden;
}

.app{
    position: absolute;
    min-width: 100vw;
    min-height: 100vh;
}

pre{
    text-wrap: wrap;
}

.cursor-pointer{
    cursor: pointer;
}

.f-size-60{
    font-size: 60px;
}
.f-size-50{
    font-size: 50px;
}
.f-size-42{
    font-size: 42px;
}
.f-size-36 {
    font-size: 36px;
}
.f-size-20{
    font-size: 20px;
}

.f-size-15{
    font-size: 15px;
}
.f-size-12{
    font-size: 12px;
}
.w-200px{
    width: 200px;
}

.h-200px{
    height: 200px;
}
.h-175px{
    height: 175px;
}

.border-line-light{
    margin: 2rem 0px;
    /* border-bottom: 1px solid rgba(256, 256, 256, 0.09); */
}

.form-select, .form-control{
    background-color: #dadada;
    border-radius: 0px;
}

label{
    color: #dadada;
    margin-bottom: 0.5rem;
    text-transform: capitalize;
}

.multi-columns{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.p-color {
    color: #e4aa48;
}

.t-color {
    color: #dadada;
}
.t-color-c5 {
    color: #c5c5c5;
}
.t-color-e5 {
    color: #e5e5e5;
}

.s-color {
    color: #2e2e35;
}

.nonModal label.s-color{
    color: #fff;
}


.modal-body label.t-color{
    color: #2e2e35;
}

.out-color {
    color: #0dadaa;
}

.p-back {
    background-color: #e4aa48;
}

.t-back {
    background-color: #dadada;
}

.t-back2{
    background-color: rgba(256, 256, 256, 0.02);
}

.t-back3{
    background-color: rgba(0, 0, 0, 0.02);
}

.s-back {
    background-color: #2e2e35;
}

.blank-background{
    position: fixed;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    background-color: rgba(0, 0, 0, 0.01);
    z-index:1;
}

.blank-background ~ .edit-delete *{
    z-index: 2;
}

.out-back {
    background-color: #0dadaa;
}

.text-justify{
    text-align: justify;
}

.navbar-dark{
    /* background-color: #2e2e35 !important; */
    --bs-navbar-color: rgba(218, 218, 218, 1) !important;
}

.collapse.show.navbar-collapse > ul.navbar-nav{
    max-height: calc(100vh - 60px);
    overflow-x: hidden;
    overflow-y: scroll;
}

.dropdown-menu.dropdown-menu-end{
    right: 0;
    background-color: rgb(228 170 72);
}

.sticky-top{
    z-index: 9999 !important;
    border-bottom: 1px solid #e4aa48;
}

.blogTech-container .nav-pills {
    --bs-nav-pills-link-active-color: #fff;
    --bs-nav-pills-link-active-bg: none;
}

.blogTech-container .nav-link{
    color: #e4aa48;
    cursor: pointer;
}

.blogTech-container .nav-pills .nav-link.active,
.blogTech-container .nav-pills .show>.nav-link, .blogTech-container .nav-link:hover {
    color: #fff;
    background-color: none;
}

.avatar{
    
    padding: 0.2rem;
    border: 1px solid #e4aa48;
    border-radius: 50px;
}

.nav-pills .nav-link.active .avatar {
    box-shadow: 0px 0px 3px 3px #e4aa48;
}

.avatar img{
    height: 1.75rem;
    width: 1.75rem;
    border-radius: 50px;
    background-color: #e4aa48;
}

.alert-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    right : 0;
    bottom: 0;
    z-index: 9999 !important;
    background-color: rgba(256,256,256,0.2);
}

.alert-wrapper .alert{
    z-index: 9999 !important;
    margin: 80px 10px 0px 10px;
}

.modal{
    padding-top: 50px;
}

/* React Quill editor start*/

.ql-editor{
    min-height: 200px;
    max-height: 75vh;
    overflow-y: auto;
}

.ql-editor-contents p{
    margin-bottom: 0px;
}

.ql-toolbar, .ql-editor {
    background-color: #dadada;
}

.ql-toolbar{
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.ql-toolbar.ql-snow .ql-formats{
    margin-right: 0px !important;
}

.ql-align-center{
    text-align: center;
}

.ql-align-center img{
    max-width: 80%;
    max-height: 150px;
}

.ql-content p{
    margin-bottom: 0.5rem !important;
}

.ql-content img {
    max-width: 80%;
    max-height: 150px;
}

.ql-content img:hover{
    -webkit-transform:scale(300%, 300%);
            transform:scale(300%, 300%);
    transition: width 2s, height 2s, -webkit-transform 2s;
    transition: width 2s, height 2s, transform 2s;
    transition: width 2s, height 2s, transform 2s, -webkit-transform 2s;
}

.ql-indent-1{
    padding-left: 40px;
}
.ql-indent-2{
    padding-left: 80px;
}
.ql-indent-3{
    padding-left: 120px;
}
.ql-indent-4{
    padding-left: 160px;
}

/* React Quill editor end*/

.sp-btn-secondary{
    background-color: #2e2e35 !important;
    border: 1px solid #e4aa48 !important;
    text-transform: uppercase !important;
    padding-top: 5px !important;
    padding-bottom: 5px !important;
    color: #e4aa48 !important;
    display: inline-block !important;
    font-weight: bold !important;
}

.sp-btn-secondary:hover{
    background-color:#e4aa48 !important;
    color: #2e2e35 !important;
}

.homebanner-image{
    background-image: url('/images/shibaprassad-profile.jpg');
    background-position: top center;
    background-repeat: no-repeat;
    background-clip: content-box;
    background-size: cover;
    height: calc(100vh - 57px);
    /* min-height: calc(100vh - 57px); */
    /* max-height: 576px; */
    display: flex;
    justify-content: center;
}

.homebanner-content{
    width: 100%;
    max-width: 100rem;
    height: 100%;
    position: relative;
}

.banner-left{
    bottom: 1rem;
    left: 1.5rem;
    position: absolute;
}

.homebanner-image .banner-right{
    right: 2.5rem;
    bottom: 1rem;
}

.banner-left, .banner-right{
    /* width: 100%;
    max-width: 360px; */
    color: white;
    text-align: center;
}

.banner-right{
    color: #e4aa48;
}

hr{
    height: 2px;
    background-color: #e4aa48;
    opacity: 1 !important;
}

.name-dot{
    display: inline-block;
    width: 15px;
    height: 15px;
    font-size: 60px;
    color: #e4aa48;
    line-height: 15px;
}

.service-category .col div, .skill-category .col div{
    transition: 0.5s all ease-in-out;
    border: 1px solid #2e2e35;
    cursor: pointer;
    color: #dadada;
    height: 100%;
}

.service-category .col div:hover, .skill-category .col div:hover {
    transition: 0.2s all ease-in-out;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background-color: #e4aa48;
    color: #2e2e35 !important;
    border-color: rgba(218,218,218,0.2);
}

.edit-delete{
display: none;
position: absolute;
top: 0.5rem;
right: 0rem;
}

.edit-delete svg:hover{
    color: #e4aa48;
    cursor: pointer;
}

.svg-inline--fa{
    height: 0.9rem;
    margin: 0px !important;
    /* background-color: #e4aa48;
    color: #2e2e35; */
    border-radius: 5px;
}

.service-item:hover .edit-delete, .skill-item:hover .edit-delete{
    display: block;
}


.primary-button{
    background-color: #2e2e35 !important;
    color: #e4aa48 !important;
    border-radius: 0px !important;
}

.primary-button:hover{
    background-color: #e4aa48;
    color: #2e2e35;
}

.primary-link{
    color: #e4aa48 !important;
    font-weight: bold;
    cursor: pointer;
}

.primary-link:hover{
    color: #2e2e35 !important;
}

.secondary-link{
    color: #dadada !important;
    cursor: pointer;
}

.secondary-link:hover, h4.secondary-link.active span{
    color: #e4aa48 !important;
}

.view-more-link{
    color: #e4aa48 !important;
    font-weight: bold;
    cursor: pointer;
}

.view-more-link:hover{
    color: #dadada !important;
}

/*********** FOOTER ****************/
.footerback{
    background-image: url('/images/my-portfolio-1_34.jpg');
    min-height: 400px;
    margin: 40px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.socialmedia-container {
    margin: 40px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    text-align: center;
}

.socialmedia-content{
    max-width: 900px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    grid-gap: 50px;
    gap: 50px;
    align-items: center;
}

.socialmedia-icons{
    height: 50px;
    width: 50px;
    background-size: cover;
    cursor: pointer;
    background-repeat: no-repeat;
}

.socialmedia-icons.fb{
    background-image: url("/images/icons_fb.jpg");
}
.socialmedia-icons.wt{
    background-image: url("/images/icons_wt.jpg");
}
.socialmedia-icons.li{
    background-image: url("/images/icons_li.jpg");
}
.socialmedia-icons.in{
    background-image: url("/images/icons_in.jpg");
}
.socialmedia-icons.tt{
    background-image: url("/images/icons_tt.jpg");
    
}

/****************BLOG********************/
.blogbanner-image{
    width: 100%;
    background-image: url("/images/blog-banner-back.jpg");
    background-size: auto 100%;
    background-repeat: repeat-x;
}


.blogbanner-image img{
    height: 150px;
    background-size: 100% auto;
}

.blogTech-container{
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
}

.blogTech-items{
    background-color: #e4aa48;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
}

.blogTech-items-logoimg{
    width: 100%;
    height: 49px;
    background-position: center left;
    background-repeat: no-repeat;
    background-size: 80px 49px;
    padding-left: 80px;
}

.blogTech-items-logoimg span{
    line-height: 40px;
}

.blogTech-items-content h3{
    display: none;
}

.blogTech-item-separator{
    height: 1px;
    width: 100%;
    border-top: 1px solid #2e2e35;
}

.component-container{
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.component-left{
    width: 100%;
    height: auto;
}

.component-left.isClose{
    display: none;
}

.component-right {
    flex: 1 1;
}

.component-left h3{
    border-bottom: 1px solid #34343b;
}

.component-left-drawer{
    display: block;
    position: fixed;
    top: 57px;
    left: 5px;
    right: 5px;
    z-index: 9999;
    display: flex;
    justify-content: center;
    height: 15px;
    font-size: larger;
    overflow-y: hidden;
    align-items: center;
    background-color: #e4aa48;
    .svg-inline--fa{
        height: 35px !important;
    }
}

.list-group, .list-group-item.disabled,
.list-group-item:disabled {
    --bs-list-group-bg: transparent !important;
    background-color: transparent !important;
    cursor: auto;
}

.list-group-item{
    color: #dadada;
    cursor: pointer;
    & .list-group-item{
        padding-right: 0 !important;
        padding-bottom: 0 !important;
    }
    & h4{
        margin: 0 !important;
    }
}

.list-group-item:hover{
    color: #e4aa48;
}
.list-group-item:hover h4{
    color: #dadada;
}



.component-right img{
    max-width: 80%;
}

.text-ellipse{
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}


/**************************** Accordian *********************/

.accordion{
    --bs-accordion-color: #fff;
    --bs-accordion-bg: rgba(256, 256, 256, 0.02);
    --bs-accordion-active-color: #e4aa48;
    --bs-accordion-active-bg: rgba(256, 256, 256, 0.05);
}

.accordion-item{
    border-color: rgba(256, 256, 256, 0.07);
}

.accordion-item .quill{
    color: rgba(0,0,0, 0.7);
}

.accordion-button{
    color:#e4aa48;
}

.accordion-button::after{
    padding: 10px;
    border: 1px solid #fff;
    background-position: center;
    background-color: rgba(256, 256, 256, 0.5);
}

.accordion-button:focus, .accordion-button:not(.collapsed){
    box-shadow:none;
    border: 1px dashed rgba(256, 256, 256, 0.07);
}

.profile .row{
    padding: 10px 0px;
}

.profile_pic{
    border: 1px solid #e4aa48;
    padding: 5px;
    max-height: 120px;
}

.edit-icon{
    position: absolute;
    right: 10px;
    top: 5px;
    background-color: rgba(256, 256, 256, 0.2);
    padding: 10px;
    border: 1px solid rgba(256, 256, 256, 0.5);
    border-radius: 50%;
    cursor: pointer;
}

.accordion .edit-icon{
    right: 0px;
}

.edit-icon:hover {
    background-color: rgba(256, 256, 256, 0.7);
    color: rgba(0, 0, 0, 0.5)
}



@media (min-width: 751px) {
    .blogTech-items {
            flex-direction: row;
    }
    .blogTech-items-logoimg {
        height: 74px;
        width: 120px;
        background-size: 120px 74px;
    }
    .blogTech-items-logoimg span{
        display: none;
    }
    .blogTech-items-content h3 {
        display: block;
    }
    .blogTech-item-separator {
        height: unset;
        width: 1px;
        border-left: 1px solid #2e2e35;
        margin: 10px 0px;
    }
    .component-container {
        flex-direction: row;
        padding-top: 0px;
        min-height: calc(100vh - 56px);
    }
    .component-left {
        max-width: 250px;
        min-height: 100%;
    }
    .component-left.isClose {
        display: block;
    }
    .component-left-drawer{
        display: none;
    }
    .svg-inline--fa {
        height: 1rem;
    }
        .blogTech-container .nav-pills .nav-link.active,
        .blogTech-container .nav-pills .show>.nav-link,
        .blogTech-container .nav-link:hover {
            color: #fff;
            background-color: rgba(256, 256, 256, 0.02);
            -webkit-transform: translateX(10px);
                    transform: translateX(10px);
            border-left: .2rem solid #e4aa48;
            padding-left: .3rem !important;
            border-radius: 0;
        }
}

.no-style-child *{
    margin: 0 !important;
    padding: 0 !important;
}

.animated-section {
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    transition: opacity 0.8s, -webkit-transform 0.8s;
    transition: opacity 0.8s, transform 0.8s;
    transition: opacity 0.8s, transform 0.8s, -webkit-transform 0.8s;
}

.hidden-section {
    opacity: 0;
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
}

.animated-element {
    opacity: 1;
    /* transform: translateX(0px); */
    transition: opacity 3s, -webkit-transform 3s;
    transition: opacity 3s, transform 3s;
    transition: opacity 3s, transform 3s, -webkit-transform 3s;
}

.hidden-element {
    opacity: 0;
    /* transform: translateX(100px); */
}

